#signupcontainer{

    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 55px;
}


#signupcontainer form{
    background-color: white;
    width: 400px;
    height: 450px;
    padding: 25px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.10);
}

#signupcontainer .input-group{
    display: flex;
    flex-direction: column;
    margin-bottom: 12px;
}

#signupcontainer .input-group label{
    margin-bottom: 10px;
    /* margin-left: px; */
    margin-top: 8px;
    color: rgb(74, 74, 74);
}

#signupcontainer .input-group input{
    padding: 11px;
    padding-left:20px;
    color: rgb(74, 74, 74);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: medium;
    border-radius: 4px;
    border: 1px solid lightgrey;
    outline:0;
}

#signcontainer .button-div{
    display: flex;
    flex-direction: column;
    align-items: center;
}

#signupcontainer button{
    background-color: rgb(146, 64, 222);
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 15px;
    margin-top: 20px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 2px;
    outline: 0;
}

#signupcontainer button:hover{
    background-color: rgb(172, 124, 220);
    cursor: pointer;
}






