
.card-box {
    background-color: white;
    width: 280px;
    padding: 25px;
    margin: 30px;
    box-shadow: -1px 1px 12px -6px rgba(0,0,0,0.72);
    border-radius: 5px;
    display: inline-block;
}


.profile-icon {
    width: 280px;
}


.nameid-label {
    color: rgb(83, 83, 83);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    cursor: default;
}

.nameid-label p{
    font-weight: 700;
}

.nameid-label span {
    font-weight: 400;
}

#view-btn {
    width: 100%;
    color: white;
    background-color: rgb(148, 64, 225);
    font-family: 'Poppins';
    padding: 10px;
    border: none;
    border-radius: 2px;
    cursor: pointer;
}

#view-btn:hover {
    background-color: rgb(190, 143, 233);
}