

#logincontainer {
    /* border: 1px solid black; */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 70px;
}

#logincontainer form{
    background-color: white;
    width: 400px;
    /* height: 350px; */
    padding: 25px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.10);
}

#logincontainer .input-group{
    display: flex;
    flex-direction: column;
    margin-bottom: 12px;
}

#logincontainer .input-group label{
    margin-bottom: 10px;
    /* margin-left: px; */
    margin-top: 8px;
    color: rgb(74, 74, 74);
}

#logincontainer .input-group input{
    padding: 12px;
    padding-left:20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: medium;
    border-radius: 4px;
    border: 1px solid lightgrey;
    outline:0;
}

#logincontainer .button-div{
    display: flex;
    flex-direction: column;
    align-items: center;
}

#logincontainer button{
    background-color: rgb(146, 64, 222);
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 15px;
    margin-top: 10px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 2px;
    outline: 0;
}

#logincontainer button:hover{
    background-color: rgb(172, 124, 220);
    cursor: pointer;
}



#logincontainer #signup-link-div p{
    margin-top: 23px;
    text-align: center;
    font-size: 14px;
    color: rgb(120, 119, 119);
}

#logincontainer #signup-link-div a{
    margin-left: 4px;
    font-size: 14px;
    color: rgb(131, 93, 167);
}

.login-error-msg{
    font-size: 12px;
    color: red;
    margin: 0;
    margin-top: 8px;
}