
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');
  /* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');




#child-homecontainer{
    display: flex;
}

#icons-div{
    /* border: 1px solid black; */
    width: 150px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#icons-div>.icon{
    /* display: flex;
    align-items: center; */
    padding-top: 40px;
    padding-left: 100px;
    font-size: 30px;
    color: rgb(102, 101, 101);
}


#content-div {
    /* border: 1px solid black; */
    width: 550px;
    height: 430px;
    background-color: white;
    position: relative;
    left: 80px;
    padding: 100px 50px 100px 0px;
}

.name-div p{
    font-family: 'Poppins', sans-serif;
    font-size: 50px;
    cursor: default;
}

.jobtitle-div {
    margin-top: -40px;
}

.jobtitle-div p{
    font-size: 20px;
    font-weight: 600;
    color: rgb(110, 109, 109);
    cursor: default;
}

.para-div p{
    font-weight: 400;
    font-size: 15px;
    line-height: 1.5;
    color: rgb(83, 83, 83);
    font-family: 'Roboto', sans-serif;
    cursor: default;
} 


.btn-div button, .btn-div a{
    margin-top: 50px;
    padding: 15px 23px 16px 23px;
    background-color: blueviolet;
    color: white;
    font-size: 17px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    border-radius: 6px;
    border: 1px solid rgb(185, 128, 241);
    cursor: pointer;
    text-decoration: none;
}

.btn-div button:hover{
    background-color: rgb(185, 128, 241);
}

#image-div{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    left: 60px;
}


#image-div img{
    margin-top: -10px;
    width: 600px;
    height: 500px;
}


/* About Starts Here */


#aboutcontainer{
   
    /* border: 1px solid black; */
    width: 100%;
    height: 720px;
    background-color: rgb(241, 240, 240);
}

#aboutme-box{
    /* border: 1px solid red; */
    text-align: center;
    padding: 15px;
}

#about-title p{
    font-family: 'Poppins';
    font-size: xx-large;
}

.small-line {
    background-color: blueviolet;   
    height: 5px;
    width:30px;
    display: flex;
    /* position: relative; */
    margin-top: -18px;
    /* left: 320px; */
    border-radius: 2px;
}

#about-subline p{
    /* width: 600px; */
    text-align: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    color: rgb(62, 61, 61);
}

/* ABOUT STARTS HERE */

#main-container{
    display: flex;
    justify-content: space-around;
}

#aboutme-content-box{
    height: 500px;
    /* border: 1px solid black; */
    width: 550px;
    padding: 20px;
    position: relative;
    left: 80px;
}

#aboutme-content-box p{
    /* border: 1px solid black; */
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    color: rgb(83, 83, 83);
    text-align: left;
    padding: 5px;
    width: 500px;
    line-height: 1.5;
    font-size: 17px;
}

#getme-title-div p{
    font-family: 'Poppins';
    font-size: 25px;
    padding-left: 10px;
    color: black;
}

#download-cv-div{
    display: flex;
}

#download-cv-div button{
    background-color: blueviolet;
    color: white;
    padding: 15px;
    position: relative;
    left: 6px;
    top: 10px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: large;
    font-weight: 600;
    border-radius: 3px;
    border: 1px solid blueviolet;
}

#download-cv-div button:hover{
    background-color: white;
    color: blueviolet;
}

#myskills-box{
    /* border: 1px solid black; */
    height: 250px;
    width: 400px;
    position:relative;
    right: 48px;
    padding: 20px;
}

#myskills-title-div {
    font-family: 'Poppins';
    font-size: 25px;
    text-align: left;
    padding-left: 10px;
}


#myskills-list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
}

.myskills-item {
    /* border: 1px solid black; */
    border-radius: 12px;
    padding: 10px 15px 10px 15px;
    margin: 0px 15px 18px 0px ;
    background-color: lightgray;
    color: rgb(64, 63, 63);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
}

#contactinfo-div{
    
    height: 200px;
    width: 400px;
    position:relative;
    right: 48px;
    top: -50px;
    padding: 20px;
    text-align: start;
}

#contactinfo-div h1{
    
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 35px;
    padding-left: 10px;
}

#contactinfo-div p{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    padding-left: 5px;
}

