#welcome-div {
    text-align: center;
}

#welcome-div p{
    font-size: 30px;
    margin: 0;
    margin-top: 8px;
}

.table-container{
    display: flex;
    justify-content: center;
}


table {
    margin: 30px auto 0 auto;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-collapse: collapse;
    width: 600px;
}

