
#main-container-calculator{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 40px;
}

#calc-mainbox{
    border: 1px solid black;
    width: 360px;
    height: 490px;
    padding: 10px;
    background-color: rgb(67, 67, 67);
    border-radius: 5px;
    box-shadow: 0px 0px 10px -2px rgba(0,0,0,0.63);
}

#result-box{
    /* border: 1px solid red; */
    width: 356px;
    height: 155px;
    padding: 2px;
    background-color: rgb(53, 52, 52);
}

#equation-div{
    /* border:1px solid green; */
    height: 30px;
    /* padding-bottom: 10px; */
}

#equation-div p{
    text-align: end;
    font-size: 20px;
    padding-right: 25px;
    position: relative;
    bottom: -10px;
    color: rgb(206, 206, 206);
    word-wrap: break-word;
}

#result-div{
    /* border:1px solid blue; */
    height: 93px;
}

#result-div p{
    margin: 0;
    font-size: 70px;
    text-align: end;
    padding-right: 20px;
    color: white;
}

#number-box{
    /* border: 1px solid red; */
    background-color: aliceblue;
    /* width: 339px; */
    /* width: 100%; */
    height: 318px;
    padding: 5px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 2px;
    background-color: rgb(53, 52, 52);

}


#number-box div {
    /* border: 1px solid blue; */
    background-color: rgb(136, 135, 135);
    cursor: pointer;
}

#number-box div p{
    text-align: center;
    font-size: 18px;
    color: rgb(241, 239, 239);
}

#number-box div:hover{
    background-color: rgb(94, 93, 93);
}

#equalto-div{
    grid-column-start: 3;
    grid-column-end: 5;
}

#number-box .equalto-div{
    background-color: blueviolet;
}

#number-box .equalto-div:hover{
    background-color: rgb(157, 85, 220);
}

